/**
 * 微信js-sdk
 * 参考文档：https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1421141115
 */
import wx from "weixin-js-sdk";
const wxApi = {
  /**
   * [wxRegister 微信Api初始化]
   * @param  {Function} callback [ready回调函数]
   */
  wxRegister(data, option) {
    //data是微信配置信息，option是分享的配置内容
    /*
    wx.config({
      debug: false, // 开启调试模式
      appId: data.appId, // 必填，公众号的唯一标识
      timestamp: data.timestamp, // 必填，生成签名的时间戳
      nonceStr: data.nonceStr, // 必填，生成签名的随机串
      signature: data.signature, // 必填，签名，见附录1
      jsApiList: [
        "checkJsApi",
        "updateTimelineShareData",
        "updateAppMessageShareData",
        "onMenuShareQQ",
        "onMenuShareWeibo"
      ] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
    });
    */
   console.log(data,option)
    wx.config(data)
    wx.ready(function() {
      // 在这里调用 API
      var shareData = {
        title: option.title,
        desc: option.desc.replace(/&nbsp:/gi,""),
        link: option.link,
        imgUrl:
          "http://vod.vguan.net/link/j52020124714574755/1c3ab863ceb2c096094020527eeca21b.jpeg"
      };
      wx.onMenuShareAppMessage(shareData);
      wx.onMenuShareTimeline(shareData);
      wx.onMenuShareQQ(shareData);
      wx.onMenuShareWeibo(shareData);
    //   
      wx.updateTimelineShareData(shareData);
      wx.updateAppMessageShareData(shareData)

      //通过checkJsApi判断当前客户端版本是否支持指定获取地理位置
      wx.checkJsApi({
        jsApiList: ["getLocation", "checkJsApi"],
        success: function(res) {
          if (res.checkResult.getLocation == false) {
            alert(
              "你的微信版本太低，不支持微信JS接口，请升级到最新的微信版本！"
            );
            return;
          }
        }
      });
    });
  }
};
export default wxApi;



